                                <p>Chatshier 會致力保護您個人資料的安全。由於任何電子傳輸方法或資料儲存方法都不是無懈可擊，我們無法擔保您的個人資訊在系統傳輸或儲存過程中百分之百安全，不被攻擊。例如，我們將您的個人資料儲存在位於受控場所的電腦系統中，並對這些系統的存取進行限制。 當我們在網際網路上傳送重要的個人資料時 (例如信用卡號碼或密碼)，我們會利用加密來保護它。 為協助保護您的隱私和個人資訊安全，我們需要您的協助。請不要將您的密碼告知他人或使用與其他服務相同的密碼。同時，若您懷疑我們的聯絡表單 出現任何未經授權使用您帳號的行為，或任何其他不安全行為，請通知我們。 我們會採取一定的措施，確保我們依據本隱私權聲明收集的資料將按照本聲明的規定和資料所在地的適用法律要求來處理。 Chatshier 如何保護我的個人資訊安全? 本網站主機均設有防火牆、防毒系統等相關的各項資訊安全設備及必要的安全防護措施，加以保護網站及您的個人資料採用嚴格的保護措施，只由經過授權的人員才能接觸您的個人資料，相關處理人員皆簽有保密合約，如有違反保密義務者，將會受到相關的法律處分。如因業務需要有必要委託其他單位提供服務時，本網站亦會嚴格要求其遵守保密義務，並且採取必要檢查程序以確定其將確實遵守。 我的資訊會在 Chatshier 保存多久？ Chatshier 在以下情形下會保留您所提供的個人資訊：您的帳號有效的情況下；履行本隱私權政策中所載的特定用途所需期間內；或適用法令中要求的期間內。 若為下列目的所合理需要，我們將在您的帳號關閉或終止您的帳號之後繼續留存您的個人資訊：</p>
